/* Nav component */
* {
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  background-color: #252525;

  background-image: url('./bilder/bg/bg-top-1.png');
  background-repeat: no-repeat;
  background-size: 70rem;

  font-family: "Spartan", Courier, monospace;
}

.wrapper {
  /* 1300px = avoid the horizontal scroll bar */
  max-width: 1300px;
}

/* html tag's*/

p {
  font-family: "Inconsolata", monospace;
  letter-spacing: 0.1em;
  line-height: 1.3em;
}

h2 {
  color: #f57f12;
  margin-bottom: 20px;
}

a {
  color: #f4e7d2;
  text-decoration: none;
  font-size: 27px;

}

::selection {
  background-color: purple;
  color: yellow;
  -webkit-text-stroke: 1px #016a53;
}
/* / html tag */

/* img */
.splash {
  background: url("./bilder/mainLeftSplash.png");
  transform: rotate(180deg);
  object-fit: cover;
  background-repeat: no-repeat;
  width: 250px;
  height: 250px;
  
  position: relative;
  top: 350px;
  left: 1150px;
}
/* Nav bar */
.flex--nav-box {
  /* MoveBoksen til Nav_Sectionen*/
  position: relative;
 left: 100px;
  display: flex;
  width: 100%; /* Litt støre en link boksen */

  position: fixed;
  z-index: 2;

  font-family: "Luckiest Guy", cursive;
 
}
.flex--nav-box > a {
  -webkit-text-stroke: 1px #252525;
  font-weight: 900;
}

/* Animation bevgelse på nav logoen */
.nav-splash-img {
  position: relative;
  animation: splash 2s ease 1s infinite alternate;
}

@keyframes splash { from { transform: scale(100%); } to { transform: scale(90%); } }

/* To make the nav bar stopp some px above */
.section-position {
  position: relative;
  top: 100px;
  width: 98.5vw;
  
}
/* To make the nav bar stopp some px above in the contactMe section */
.section-position-higher {
  position: relative;
  top: 200px;
}

/* / Animation bevgelse på nav logoen */

.flex--nav-link {
  position: absolute;

  width: 800px; /* Bredden link boksen */
  
  top: 0;
  height: 70px;
 
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
}

.nav-icon-home { color: #f57f12; }
.letter-space { letter-spacing: 0.6rem; }

.nav-icon-home:hover,
.nav-icon-about-me:hover,
.nav-icon-skills:hover,
.nav-icon-prosjekter:hover,
.nav-icon-contact-me:hover {
  transform: scale(105%);
  font-weight: 900;
}

/* / Nav bar */

/* |^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ Home | 
   |.....................................|*/

.intro {
  position: relative;
  padding: 10px;
  width: 90%;
  height: 183px;
  left: 110px;
  top: 320px;
  background-color: transparent;

  font-family: "Luckiest Guy", cursive;
  letter-spacing: 0.5rem;
}
.intro > h2 { color: #f4e7d2; }

.whiteStroke {
  -webkit-text-stroke: 1px white;
  font-size: 45px;
}
.orangeStroke { font-size: 30px; }

.intro--skill--img {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 300px;
}
.intro--skill--img > img { border-radius: 50%; }

/*  icon */
.iconHTML,
.iconCSS,
.iconFIGMA,
.iconJS,
.iconREACT,
.iconTypeScript {
  width: 40px;
  animation-name: icons;
  animation-duration: 5s;
  animation-timing-function: ease-out;
  animation-delay: 2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;

  cursor: pointer;
}

@keyframes icons {
  from {
  }
  to {
    margin-left: 100px;
  }
}

/* When hover at skill icon's at <home /> */
.iconHTML:hover,
.iconCSS:hover,
.iconFIGMA:hover,
.iconJS:hover,
.iconREACT:hover,
.iconTypeScript:hover {
  transform: scale(120%);
  box-shadow: 0 0 5px #f4e7d2;
}

/* when icon get's clicked */
.toggleDiv {
  position: relative;
  top: 20px;
  width: 420px;

  background-color: #666;
  border: 2px dotted #f57f12;
  border-radius: 20px;

  color: #f4e7d2;
  padding: 20px;

  animation: fade-down 2000ms forwards;
  transform: translateY(-100px);
  opacity: 0;
  z-index: 2;
}

/* To make the divBox drop down */
@keyframes fade-down {
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
#pop-html{ left: 100px; }
#pop-css{ left: 240px; }
#pop-figma{ left: 380px; }
#pop-js{ left: 520px; }
#pop-react{ left: 660px; }
#pop-ts{ left: 800px; }

.toggleDiv > h2 { font-size: 24px; }

.col_orange { color: #f57f12; }
