  max-width: 700px;
}
/* Router dom, grid setup */
.routerCounter { grid-column: span 2; }
.routerDrumkit { grid-column: span 2; }
.routerToDoList { grid-column: span 2; }
.routerNettStore { grid-column: 2 / 4; grid-row-start: 2; }
.routerNew { grid-column: 4 / 6; grid-row-start: 2; }

/* Router Link Knappene */
.Nav-header > a {
